.LoginScreen-container {
  z-index: 10;
  position: absolute;
  right: 0;
  left: 0;
  height: 100%;
  background-color: #8f3b3b;

}

.box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap; /* Optional. only if you want the items to wrap */
    -ms-flex-pack: center;
        justify-content: center; /* For horizontal alignment */
    -ms-flex-align: center;
        align-items: center; /* For vertical alignment */
}

.login-input {
  opacity: 0.8;
  -webkit-box-shadow: #0000004d 0px 0px 30px;
          box-shadow: #0000004d 0px 0px 30px;
}

.login-form {
  -webkit-transition: all 1s linear;
  -o-transition: all 1s linear;
  transition: all 1s linear;
  background-color: #0003;
  padding: 3rem;
  border-radius: 4px;
}

.status-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 22px;
  background-color: #0003;
  color: #ffffffe6;
  display: -ms-flexbox;
  display: flex;
  padding: 3px 10px 3px 10px;
  -webkit-box-shadow: #0000004d 0px 2px 1px;
          box-shadow: #0000004d 0px 2px 1px;
  font-size: 12px
}



